/**
 * Custom modal with Save, Submit to Teacher, and Cancel buttons
 *
 * @module     mod_projetvet/modal_save_submit
 * @copyright  2025 Bas Brands <bas@sonsbeekmedia.nl>
 * @license    http://www.gnu.org/copyleft/gpl.html GNU GPL v3 or later
 */

import Modal from 'core/modal';
import * as ModalEvents from 'core/modal_events';

export default class ModalSaveSubmit extends Modal {
    static TYPE = 'mod_projetvet/modal_save_submit';
    static TEMPLATE = 'mod_projetvet/modal_save_submit';

    /**
     * Custom event for submit to teacher action
     */
    static SUBMIT_TO_TEACHER = 'mod_projetvet_submit_to_teacher';

    configure(modalConfig) {
        modalConfig.show = true;
        modalConfig.removeOnClose = true;
        super.configure(modalConfig);
    }

    registerEventListeners() {
        super.registerEventListeners();

        // Listen for save button click (draft save)
        this.getModal().on('click', this.getActionSelector('save'), (e) => {
            e.preventDefault();
            const saveEvent = ModalEvents.save;
            this.triggerEvent(saveEvent, this);
        });

        // Listen for submit button click (submit to teacher)
        this.getModal().on('click', this.getActionSelector('submit'), (e) => {
            e.preventDefault();
            this.triggerEvent(ModalSaveSubmit.SUBMIT_TO_TEACHER, this);
        });

        // Listen for cancel button click
        this.getModal().on('click', this.getActionSelector('cancel'), (e) => {
            e.preventDefault();
            const cancelEvent = ModalEvents.cancel;
            this.triggerEvent(cancelEvent, this);
        });
    }

    getActionSelector(action) {
        return `[data-action="${action}"]`;
    }
}
